.c-scrollbar {
  position: absolute;
  right: 0;
  top: 0;
  width: 11px;
  height: 100vh;
  transform-origin: center right;
  transition: transform 0.3s, opacity 0.3s;
  opacity: 0;

  &:hover {
    transform: scaleX(1.45);
  }

  &:hover,
  .has-scroll-scrolling &,
  .has-scroll-dragging & {
    opacity: 1;
  }
}

.c-scrollbar_thumb {
  position: absolute;
  top: 0;
  right: 0;
  background-color: $dark-grey;
  border-radius: 0;
  opacity: 1;
  width: 5px;
  margin: 2px;
  cursor: grab;

  .has-scroll-dragging & {
    cursor: grabbing;
  }
}
